<script>
export default {
  name: 'DoctorList',
  data() {
    return {
      department: '',
    }
  },
  created() {
    // 获取路由参数中的科室信息
    this.department = this.$route.query.department || '';
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    goToAppointment(doctorName, doctorTitle, rating, date) {
      // 获取医生的状态（有号/无号）
      let hasAppointment = doctorName !== '布莱恩博士'; // 假设只有布莱恩博士无号
      let doctorImage = '';
      
      // 根据医生名字确定图片路径
      if (doctorName === '布莱恩博士') {
        doctorImage = 'doctor1.png';
      } else if (doctorName === '柳叶博士') {
        doctorImage = 'doctor2.png';
      } else if (doctorName === '夏雨博士') {
        doctorImage = 'doctor3.png';
      } else if (doctorName === '张一统博士') {
        doctorImage = 'doctor4.png';
      }
      
      // 根据职称确定挂号费用
      let fee = 10; // 默认费用
      if (doctorTitle === '主任医师') {
        fee = 20;
      } else if (doctorTitle === '副主任医师') {
        fee = 15;
      } else if (doctorTitle === '主治医师') {
        fee = 10;
      }
      
      // 跳转到挂号缴费页面，并传递医生信息
      this.$router.push({
        path: '/appointment-payment',
        query: {
          doctorName: doctorName,
          doctorTitle: doctorTitle,
          rating: rating,
          date: date,
          hasAppointment: hasAppointment,
          doctorImage: doctorImage,
          fee: fee
        }
      });
    }
  }
}
</script>

