<script>
import { ref, onMounted } from 'vue';
import { Swiper, SwiperSlide } from 'swiper/vue';
import { Pagination } from 'swiper/modules';
import { useRouter } from 'vue-router'; // 确保导入 useRouter
import 'swiper/css';
import 'swiper/css/pagination';

export default {
  name: 'GuidePages',
  components: {
    Swiper,
    SwiperSlide
  },
  setup() {
    const activeIndex = ref(0);
    const swiperInstance = ref(null);
    const modules = [Pagination]; // 使用Pagination模块
    const router = useRouter(); // 获取路由实例
    
    const onSwiper = (swiper) => {
      swiperInstance.value = swiper;
    };
    
    const onSlideChange = () => {
      if (swiperInstance.value) {
        activeIndex.value = swiperInstance.value.activeIndex;
      }
    };
    
    const goToSlide = (index) => {
      if (swiperInstance.value) {
        swiperInstance.value.slideTo(index);
      }
    };
    
    const startApp = () => {
      console.log('点击立即体验按钮'); // 添加日志
      router.push('/login'); // 跳转到登录页面
    };
    
    // 使用onMounted确保组件挂载后初始化
    onMounted(() => {
      console.log('Guide pages component mounted');
    });
    
    return {
      activeIndex,
      modules,
      onSwiper,
      onSlideChange,
      goToSlide,
      startApp
    };
  }
};
</script>