/* eslint-disable */
import { createRouter, createWebHashHistory } from 'vue-router'
import GuidePages from '../components/GuidePages.vue'
import LoginPage from '../components/LoginPage.vue'
import HomePage from '../components/HomePage.vue'
import RegisterNotice from '../components/RegisterNotice.vue'
import AddPatient from '../components/AddPatient.vue'
import DepartmentSelect from '@/components/DepartmentSelect.vue'
import DoctorList from '@/components/DoctorList'
import AppointmentPayment from '../components/AppointmentPayment.vue'
import PaymentSuccess from '../components/PaymentSuccess.vue'
import Navigation from '../components/Navigation.vue'
import MedicalReminder from '../components/MedicalReminder.vue'


const routes = [
  {
    path: '/',
    name: 'Guide',
    component: GuidePages
  },
  {
    path: '/login',
    name: 'Login',
    component: LoginPage
  },
  {
    path: '/home',
    name: 'Home',
    component: HomePage
  },
  {
    path: '/register-notice',
    name: 'RegisterNotice',
    component: RegisterNotice
  },
  {
    path: '/add-patient',
    name: 'AddPatient',
    component: AddPatient
  },
  {
    path: '/department-select',
    name: 'DepartmentSelect',
    component: DepartmentSelect
  },
  {
    path: '/doctor-list',
    name: 'DoctorList',
    component: DoctorList
  },
  {
    path: '/appointment-payment',
    name: 'AppointmentPayment',
    component: AppointmentPayment
  },
  // 在routes数组中添加
  {
    path: '/payment-success',
    name: 'PaymentSuccess',
    component: PaymentSuccess
  },
  // 在routes数组中添加
  {
    path: '/medical-reminder',
    name: 'MedicalReminder',
    component: MedicalReminder
  },
  {
    path: '/navigation',
    name: 'Navigation',
    component: Navigation
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
