        <img src="../assets/load3.png" alt="进度条" class="progress-img" />
      </div>
    </div>

    <!-- 返回按钮和标题 -->
    <div class="header">
      <div class="back-button" @click="goBack">
        <img src="../assets/back.png" alt="返回" class="back-img" />
      </div>
      <div class="header-title">挂号缴费</div>
    </div>

    <!-- 确认挂号信息 -->
    <div class="confirmation-card">
      <div class="card-title">确认挂号信息</div>
      
      <div class="doctor-info">
        <div class="doctor-avatar">
          <img :src="require(`../assets/${doctorImage}`)" alt="医生头像" />
        </div>
        <div class="doctor-details">
          <div class="doctor-name-container">
            <div class="doctor-name">{{ doctorName }}</div>
            <div :class="hasAppointment ? 'has-appointment' : 'no-appointment'">
              {{ hasAppointment ? '有号' : '无号' }}
            </div>
          </div>
          <div class="doctor-title">{{ doctorTitle }}</div>
          <div class="rating-and-select">
            <div class="rating-stars">
              <span v-for="i in 5" :key="i" class="star filled">★</span>
            </div>
            <div class="select-other" @click="goToDoctorList">选择其他医生 ></div>
          </div>
        </div>
      </div>

      <div class="appointment-details">
        <div class="detail-item">
          <div class="detail-label">就诊日期：</div>
          <div class="detail-value">{{ date }}</div>
        </div>
        <div class="detail-item">
          <div class="detail-label">就诊人姓名：</div>
          <div class="detail-value">张*</div>
        </div>
        <div class="detail-item">
          <div class="detail-label">就诊人身份证：</div>
          <div class="detail-value">430***********1598</div>
        </div>
        <div class="detail-item">
          <div class="detail-label">就诊人手机号：</div>
          <div class="detail-value">150******12</div>
        </div>
      </div>

      <div class="payment-amount">
        <div class="amount-label">挂号费用：</div>
        <div class="amount-value">¥{{ fee }}</div>
      </div>
    </div>

    <!-- 支付方式 -->
    <div class="payment-methods">
      <div class="section-title">请选择支付方式</div>
      
      <div class="method-list">
        <div class="method-item selected">
          <div class="method-icon wechat">
            <img src="../assets/wechat.png" alt="微信支付" />
          </div>
          <div class="method-name">微信支付</div>
          <div class="method-check">
            <img src="../assets/check.png" alt="已选择" />
          </div>
        </div>
        
        <div class="method-item">
          <div class="method-icon alipay">
            <img src="../assets/alipay.png" alt="支付宝" />
          </div>
          <div class="method-name">支付宝</div>
          <div class="method-check">
            <div class="unchecked-circle"></div>
          </div>
        </div>
        
        <div class="method-item">
          <div class="method-icon bank">
            <img src="../assets/bank.png" alt="网银支付" />
          </div>
          <div class="method-name">网银支付</div>
          <div class="method-check">
            <div class="unchecked-circle"></div>
          </div>
        </div>
        
        <div class="method-item">
          <div class="method-icon apple">
            <img src="../assets/apple.png" alt="Apple Pay" />
          </div>
          <div class="method-name">Apple Pay</div>
          <div class="method-check">
            <div class="unchecked-circle"></div>
          </div>
        </div>
      </div>
    </div>

    <!-- 底部按钮 -->
    <!-- 底部按钮 -->
    <div class="bottom-buttons" v-if="!hasAppointment">
      <div class="no-appointment-btn">暂无余号</div>
      <div class="back-btn" @click="goBack">返回</div>
    </div>
    <div class="bottom-buttons" v-else>
      <div class="fee-display">挂号费用：¥{{ fee }}</div>
      <div class="payment-btn" @click="payNow">立即支付</div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AppointmentPayment',
  data() {
    return {
      doctorName: '',
      doctorTitle: '',
      rating: 0,
      date: '',
      hasAppointment: false,
      doctorImage: ''
    }
  },
  created() {
    // 获取路由参数中的医生信息
    this.doctorName = this.$route.query.doctorName || '布莱恩博士';
    this.doctorTitle = this.$route.query.doctorTitle || '主任医师';
    this.rating = this.$route.query.rating || 5.0;
    this.date = this.$route.query.date || '2025.3.19';
    this.hasAppointment = this.$route.query.hasAppointment === 'true' || false;
    this.doctorImage = this.$route.query.doctorImage || 'doctor1.png';
    this.fee = parseInt(this.$route.query.fee) || 20;
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    goToDoctorList() {
      this.$router.push('/doctor-list');
    },
    payNow() {
      // 跳转到支付成功页面，并传递相关信息
      this.$router.push({
        path: '/payment-success',
        query: {
          fee: this.fee,
          patientName: '李*',
          date: this.date,
          department: '神经内科',
          doctorName: this.doctorName.replace('博士', '')
        }
      });
    }
  }
}
</script>

